#white-flash.normal {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity :1;
  -webkit-transition:opacity .9s ease-out;
  -o-transition:opacity .9s ease-out;
  transition:opacity .9s ease-out;
}

#white-flash.do-transition {
  opacity: 0;
  background: white;
}



